/* ===============================================
    14.Calendar
------------------------*/
.calendar { 
    text-align: center;
    width: 100%;
}
.calendar header { 
    padding: 36px 20px;
    font-size: 2.2rem;
    font-weight: 500;
    position: relative;
} 
.calendar header span { 
    display: inline-block; 
    line-height: 20px;
    font-size: 20px; 
}
.calendar header .button { 
    width: 40px;
    line-height: 40px;
    text-align: center;
    position: absolute;
    cursor: pointer;
    top: 27px;
    color: #fff;
} 
.calendar header .right.button { right:0; }
.calendar header .left.button { left: 0; }
.calendar header .header-label { 
    font-size: 22px;
    line-height: 22px;
    font-weight: 500;
    text-transform: uppercase;
}
.calendar table { 
    margin: 0;
    background:#fff; 
    border-collapse: collapse; 
} 
.calendar thead th { 
    width: 12%;
    border: 1px solid #e5e5e5;
    padding: 10px 0;
    font-weight: 400;
    text-transform: uppercase; 
    position: relative;
} 
.calendar thead th:not(:last-child) { 
    border-right:1px solid #fff; 
}
.calendar table td {
    overflow: hidden;
    position: relative;
    font-size: 20px;
    color: #333;
    padding: 0 !important;
    margin: 0 !important;
    border-right: 1px solid #e5e5e5;
    height: 160px;
}
.calendar .calendar-frame tbody td::after{
    position: absolute;
    content: '';
    display: block;
    border-radius: 30px;
    width: 50px;
    height: 50px;
    left: 0;
    top: 55px;
    right: 0;
    margin: 0 auto;
    border: 2px solid transparent;
}
.calendar .calendar-frame tbody td:not(.disabled).selected { 
    background:#eee; 
    color:#555;  
} 
.calendar .calendar-frame tbody td:hover { 
    cursor: pointer;
}
.calendar .calendar-frame tbody td:last-child {
    cursor: not-allowed;
    background: #f5f5f5 !important;
    color: #ccc !important;
}
.calendar .calendar-frame tbody td:last-child::after{
    border: #f5f5f5 !important;
}


.calendar.small header {
    padding: 8px 20px 12px;
    font-size: 1.8rem;
}
.calendar.small header .button{ top:0; }
.calendar.small table td{ height: 45px; font-size: 14px; }
