.header {
  background-color: #033b4a;
  color: white;
}

.header ul li {
  list-style-type: none;
  display: inline-block;
}

.header ul {
  margin: 0;
  padding: 0;
}

.HeaderContainer {
  font-size: 13px;
  list-style-type: none;
  height: 48px;
}

.HeaderContainer>ul {
  margin: 0;
}

.social:hover {
  color: #01d6a3;
}

